<script>
import UplusApi from '@uplus/uplus-api';
import utils from '@/utils/utils'
import personalApi from '@/http/service/index';
import semver from '@/utils/semver'

const instance = new UplusApi()
instance.initDeviceReady()
export default {
  mounted() {
    if (this.$route.query.cOrderSn) {
      this.cOrderSn = this.$route.query.cOrderSn
    }
    this.listData()
  },
  data() {
    return {
      shop: [
        {
          productName: '海尔 滚筒洗衣机',
          cOrderSn: 'DSAG80628BKX1滚筒洗衣机 DSAG81',
          productImgUrl: '@/assets/imgs/pc/camera.png',
        },
        {
          productName: '海尔 滚筒洗衣机',
          cOrderSn: 'DSAG80628BKX1滚筒洗衣机 DSAG82',
          productImgUrl: '@/assets/imgs/pc/camera.png',
        },
        {
          productName: '海尔 滚筒洗衣机',
          cOrderSn: 'DSAG80628BKX1滚筒洗衣机 DSAG83',
          productImgUrl: '@/assets/imgs/pc/camera.png',
        }
      ],
      cOrderSn: 'WD192028706242', // 网单号
    }
  },
  methods: {
    /**
   * @description  去评价
   */
    toEvaluate(cOrderSn) {
      this.$router.push({ path: '/evaluate', query: { cOrderSn: cOrderSn } })
      // uni.navigateTo({
      //   url: '/subPages/personalCenter/comment/evaluate?cOrderSn=' + cOrderSn
      // });
    },
    /**
   * @description  去首页
   */
    toHome() {
      if (utils.isHaierApp()) {
        instance.initDeviceReady().then(() => {
          instance.upAppinfoModule.getAppInfo().then(result => {
            let appVersion = result.retData.appVersion
            console.log(appVersion, 'appVersion')
            if (appVersion && semver.gte(appVersion, '6.20.0')) {
              console.log('111')
              this.$util.openNewWindow('https://uplus.haier.com/uplusapp/customize/index.html?close_current_page=true', true, true)
            } else {
              console.log('222')
              instance.initDeviceReady().then(function() {
                instance.closeH5ContainerView();
              });
            }
          })
        })
      } else {
        this.$router.push({ path: '/' })
      }
      // uni.switchTab({
      //   url: '/pages/main/index'
      // });
    },
    /**
   * @description  评价成功接口
   */
    listData() {
      const data = {
        cOrderSn: this.cOrderSn // 网单号
      }
      personalApi.commentSuccessPage(data).then(res => {
        if (res.data.success) {
          this.shop = res.data.data.waitComments
        } else {
          this.$toast.fail(res.data.message)
        }
      })
    }
  }
}
</script>
